@font-face {
  font-family: 'lato';
  src: url('./fonts/lato-light-webfont.woff') format('woff'),
       url('./fonts/lato-light-webfont.svg#latolight') format('svg');
  font-weight: 300;
  font-style: normal;
}

@font-face {
  font-family: 'lato';
  src: url('./fonts/lato-regular-webfont.woff') format('woff'),
       url('./fonts/lato-regular-webfont.svg#latolight') format('svg');
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: 'lato';
  src: url('./fonts/lato-italic-webfont.woff') format('woff'),
       url('./fonts/lato-italic-webfont.svg#latoitalic') format('svg');
  font-weight: normal;
  font-style: italic;
}

@font-face {
  font-family: 'lato';
  src: url('./fonts/lato-lightitalic-webfont.woff') format('woff'),
       url('./fonts/lato-lightitalic-webfont.svg#latolight_italic') format('svg');
  font-weight: 300;
  font-style: italic;
}

@font-face {
  font-family: 'lato';
  src: url('./fonts/lato-hairlineitalic-webfont.woff') format('woff'),
       url('./fonts/lato-hairlineitalic-webfont.svg#latohairline_italic') format('svg');
  font-weight: 100;
  font-style: italic;
}

@font-face {
  font-family: 'lato';
  src: url('./fonts/lato-hairline-webfont.woff') format('woff'),
       url('./fonts/lato-hairline-webfont.svg#latohairline') format('svg');
  font-weight: 100;
  font-style: normal;
}

@font-face {
  font-family: 'lato';
  src: url('./fonts/lato-black-webfont.woff') format('woff'),
       url('./fonts/lato-black-webfont.svg#latoblack') format('svg');
  font-weight: 800;
  font-style: normal;
}

@font-face {
  font-family: 'lato';
  src: url('./fonts/lato-bolditalic-webfont.woff') format('woff'),
       url('./fonts/lato-bolditalic-webfont.svg#latobold_italic') format('svg');
  font-weight: 700;
  font-style: italic;
}

@font-face {
  font-family: 'lato';
  src: url('./fonts/lato-bold-webfont.woff') format('woff'),
       url('./fonts/lato-bold-webfont.svg#latobold') format('svg');
  font-weight: 700;
