}

@font-face {
  font-family: 'lato';
  src: url('./fonts/lato-blackitalic-webfont.woff') format('woff'),
       url('./fonts/lato-blackitalic-webfont.svg#latoblack_italic') format('svg');
  font-weight: 800;
  font-style: italic;
}

strong,
b {
  font-family: lato;
  font-weight: bold;
}
em,
i {
  font-family: lato;
  font-style: italic;
}
