.nx-current-user-button:not(:first-child) {
  margin-left: 24px; }

.nx-current-user-button__popover .nx-popover__body {
  padding: 0; }

.nx-current-user-button__initials {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 32px;
  height: 32px;
  border-radius: 50%;
  background-color: #314358;
  font-size: 12px;
  font-weight: bold;
  color: #fff;
  cursor: pointer;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none; }

.nx-current-user-dropdown {
  width: 184px;
  padding: 5px 0; }
  .nx-current-user-dropdown-item {
    box-sizing: border-box;
    display: flex;
    align-items: center;
    width: 100%;
    height: 40px;
    padding: 0 24px;
    border: none;
    overflow: hidden;
    background: none;
    font-size: 14px;
    font-weight: normal;
    font-style: normal;
    text-align: left;
    text-decoration: inherit;
    text-overflow: ellipsis;
    white-space: nowrap;
    color: #98a1ac;
    cursor: pointer;
    transition: 0.2s cubic-bezier(0.22, 0.61, 0.36, 1); }
    .nx-current-user-dropdown-item:hover {
      background-color: #3d85d1;
      color: #fff; }
