.nx-global-header-icon {
  box-sizing: content-box;
  display: inline-block;
  padding: 8px; }
  .nx-global-header-icon__popover-body .nx-popover__body {
    padding: 0; }
  .nx-global-header-icon__icon {
    display: inline-block;
    position: relative;
    width: 18px;
    height: 18px;
    vertical-align: top; }
  .nx-global-header-icon__badge {
    content: ' ';
    position: absolute;
    top: 0;
    right: 0;
    margin: -1px -3px;
    padding: 4px;
    border-radius: 50%;
    background: #3d84d1; }
  .nx-global-header-icon svg {
    display: block;
    width: 100%;
    height: 100%; }
  .nx-global-header-icon path {
    transition: 0.15s fill cubic-bezier(0.4, 0, 1, 1); }
  .nx-global-header-icon--focused path, .nx-global-header-icon:hover path {
    fill: #314358; }
