.nx-support-widget {
  box-sizing: content-box;
  width: 224px;
  padding: 5px 0;
  font-size: 14px;
  color: #98a1ac; }
  .nx-support-widget__title {
    box-sizing: content-box;
    height: 16px;
    padding-top: 4px;
    font-size: 10px;
    text-align: center;
    text-transform: uppercase; }
  .nx-support-widget__hr {
    margin: 0 16px 8px;
    border-bottom: 1px solid #ccc; }
  .nx-support-widget__option {
    box-sizing: content-box;
    display: flex;
    align-items: center;
    height: 40px;
    padding: 0 16px;
    text-decoration: none;
    color: #98a1ac;
    cursor: pointer; }
    .nx-support-widget__option-text--blue {
      color: #5db1e2; }
    .nx-support-widget__option-text--strong {
      font-weight: 700; }
  .nx-support-widget__option:hover {
    background-color: #3d85d1;
    color: #fff;
    transition: 0.2s cubic-bezier(0.22, 0.61, 0.36, 1); }
  .nx-support-widget__option:hover .nx-support-widget__option-text--blue {
    color: #fff;
    transition: 0.2s cubic-bezier(0.22, 0.61, 0.36, 1); }

.header-action--focused path {
  fill: #314358; }
