.nx-global-header {
  box-sizing: border-box;
  flex-grow: 0;
  flex-shrink: 0;
  min-width: 320px;
  height: 50px;
  border-bottom: 2px solid #e5e5e5;
  background-color: #fff; }
  .nx-global-header, .nx-global-header__wrapper {
    display: flex; }
  .nx-global-header__wrapper {
    width: 100%;
    padding: 0 16px 0 32px; }
  .nx-global-header__content {
    display: flex;
    align-items: center;
    justify-content: flex-end;
    width: 100%;
    padding: 0 18px 0 0; }

@media (min-width: 1366px) {
  .nx-global-header__wrapper {
    padding: 0 16px 0 40px; } }

@media (min-width: 1920px) {
  .nx-global-header__wrapper {
    padding: 0 16px 0 80px; } }
